<template>
    <el-dialog
    width="800px"
    title="编辑产品模版"
    :visible.sync="isVisible"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    @close="cancel"
    center
    >
       <el-form :inline="true" ref="templateForm" :model="templateForm" :rules="formRules" label-width="80px">
            <!-- <el-row v-show="false">
               <el-col :span="12">
                    <el-form-item label="id" prop="productUuid">
                        <el-input v-entity="templateForm.productUuid" />
                    </el-form-item>
               </el-col>
           </el-row> -->
            <el-row>
               <el-col :span="12">
                 <el-form-item label="商户" prop="productMerchant" >
                   <el-select v-model="templateForm.productMerchant" style="width:178px" placeholder="请选择商户">
                     <el-option
                       :disabled="item.disabled"
                       v-for="(item, index) in merchantsList"
                       :key="index"
                       :label="item.userName"
                       :value="item.userUuid"
                     />
                   </el-select>
                 </el-form-item>
               </el-col>
               <el-col :span="12">
                    <el-form-item label="品牌" prop="productBrand">
                      <el-select v-model="templateForm.productBrand" filterable="" allow-create="" default-first-option style="width:178px" placeholder="请选择品牌">
                        <el-option
                          v-for="(item, index) in brandList"
                          :key="index"
                          :label="item"
                          :value="item"
                        />
                      </el-select>
                    </el-form-item>
               </el-col>
           </el-row>
           <el-row>
               <el-col :span="12">
                    <el-form-item label="货品名称" prop="productName">
                        <el-input v-model="templateForm.productName" />
                    </el-form-item>
               </el-col>
               <el-col :span="12">
                    <el-form-item label="型号" prop="productModel">
                        <el-input v-model="templateForm.productModel" />
                    </el-form-item>
               </el-col>
           </el-row>
           <el-row>
               <el-col :span="12">
                    <el-form-item label="规格" prop="productStandard">
                        <el-input v-model="templateForm.productStandard" />
                    </el-form-item>
               </el-col>
               <el-col :span="12">
                    <el-form-item label="颜色" prop="productColor">
                        <el-input v-model="templateForm.productColor" />
                    </el-form-item>
               </el-col>
           </el-row>
           <el-row>
               <el-col :span="12">
                    <el-form-item label="包装数量" prop="productCount">
                      <el-input-number v-model="templateForm.productCount" controls-position="right" :min="1" :precision="0" :step="1" :max="99999" size="mini"></el-input-number>
                    </el-form-item>
               </el-col>
             <el-col :span="12">
               <el-form-item label="允许混搭" prop="productMixtrue">
                 <el-select v-model="templateForm.productMixtrue" style="width:178px">
                   <el-option label="是" value="1"></el-option>
                   <el-option label="否" value="0"></el-option>
                 </el-select>
               </el-form-item>
             </el-col>
               <!--<el-col :span="12">-->
                    <!--<el-form-item label="计件类型" prop="productMain">-->
                        <!--<el-input v-model="templateForm.productMain" maxlength="50" />-->
                    <!--</el-form-item>-->
               <!--</el-col>-->
           </el-row>
         <el-row>
           <el-col :span="12">
             <el-form-item label="是否质检" prop="productChecked">
               <el-select v-model="templateForm.productChecked" :disabled="templateForm.productType == '0'" style="width:178px">
                 <el-option label="是" value="1"></el-option>
                 <el-option label="否" value="0"></el-option>
               </el-select>
             </el-form-item>
           </el-col>
         </el-row>
            <el-row>
              <el-col :span="12" :offset="3">
              <el-table
                ref="orderTable"
                stripe
                max-height="200"
                empty-text="无子件"
                :data="templateForm.productItems">
                <el-table-column  label="编号" type="index" header-align="center">
                </el-table-column>
                <el-table-column  label="子件名"  header-align="center">
                  <template slot-scope="scope">
                    <el-input v-model="scope.row.productItemName" maxlength="50" ></el-input>
                  </template>
                </el-table-column>
                <el-table-column
                  width="100px"
                  :render-header="renderHeader">
                  <template slot-scope="scope">
                    <el-button
                      @click.native.prevent="delChild(scope.$index)"
                      type="text"
                      rules="small">
                      删除
                    </el-button>
                  </template>
                </el-table-column>
              </el-table>
              </el-col>
           </el-row>
       </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button size="small" type="primary" @click="save">提交</el-button>
          <el-button size="small" @click="cancel">取消</el-button>
        </div>
    </el-dialog>
</template>
<script>
/**
 * Template Edit Component
 * ----------------------
 * Author: Calamus
 * Date: 2018.08.28
 */
// validate
import Validator from '@/utils/extendValidate/extendValidate'
// api
import { editTemplate } from '@/api/warehouse'
import { setSession, getSession } from '@/utils/session'

export default {
  name: 'TemplateEdit',
  data () {
    return {
      isVisible: false,
      templateForm: {
        warehouseUuid:'',
        productUuid:'',
        productMerchant:'',
        productBrand:'',
        productName:'',
        productModel:'',
        productStandard:'',
        productColor:'',
        productCount:'',
        productMain:'',
        productType:'',
        productMixtrue:'1',
        productItems:[],
        productCost:'',
        productChecked:'0'
      },
      formRules: {
        productMerchant: [{required: true, message: '请输入商户', trigger: 'blur'}],
        productBrand: [{required: true, message: '请输入品牌', trigger: 'blur'}],
        productName: [{required: true, message: '请输入货品名称', trigger: 'blur'}],
        productModel: [{required: true, message: '请输入型号', trigger: 'blur'}],
        // productStandard: [{required: true, message: '请输入规格', trigger: 'blur'}],
        // productColor: [{required: true, message: '请输入颜色', trigger: 'blur'}],
        productCost: [{required: true, message: '请输入计件收费', trigger: 'blur'},
              {validator: Validator.isPrice,message: '请输入正确的价格', trigger: 'blur'}],
        productCount: [{required: true, message: '请输入包装数', trigger: 'blur'},
               {validator: Validator.isPositiveInteger,message: '请输入正确的数量', trigger: 'blur'}],
        // productMain: [{required: true, message: '请输入计件类型', trigger: 'blur'}],
        productType: [{required: true, message: '请选择产品类型', trigger: 'blur'}],
        productMixtrue: [{required: true, message: '请选择是否允许混搭', trigger: 'blur'}],
        productChecked: [{required: true, message: '请选择质检方式', trigger: 'blur'}],
      }
    }
  },
  props: {
    show: {
      type: Boolean,
      default: false
    },
    modifyTemplate: {
      type: Object
    },
    merchantsList: {
      type: Array
    },
    brandList: {
      type: Array
    }
  },
  watch: {
    show (isShow) {
      this.isVisible = isShow
      if(isShow){
        this.templateForm = JSON.parse(JSON.stringify(this.modifyTemplate))
        if(this.templateForm.productType == '0'){
          this.templateForm.productChecked = '0'
        }
      }
    },
    // modifyTemplate (user) {
    //   // 显示对话框后,如果为编辑状态(有用户数据传入),则合并表单数据
    //   if (user) {
    //     for (let [key, value] of Object.entries(user)) {
    //       if (this.templateForm[key] !== undefined && key != "productItems")
    //         this.templateForm[key] = value
    //       if(key == "productItems")
    //       this.templateForm.productItems = []
    //       this.templateForm.productItems = [].concat(user.productItems)
    //     }
    //     if(this.templateForm.productType == '0'){
    //       this.templateForm.productChecked = '0'
    //     }
    //   }
    // }
  },
  methods: {
    save () {
      this.$refs.templateForm.validate(isValid => {
        if (isValid) {
          const params = Object.assign({}, this.templateForm)
          editTemplate(params).then(res => {
            if (!res.error) {
              this.$message({
                message: '编辑模版成功!',
                type: 'success'
              })
              this.cancel()
              this.$emit('getBrandModel',{warehouseUuid:getSession('warehouseUuid')})
              this.$emit('updateList')
            }
          })
        }
      })
    },
    cancel () {
      this.$refs.templateForm.resetFields()
      this.$emit('update:show', false)
      this.$emit('update:modifyTemplate', null)
      this.isVisible = false
    },
    renderHeader (createElement, {}) {
      return createElement(
        'el-button', {
          'class': ['addBtn'],
          domProps: {
            innerHTML: '新增子件'
          },
          on: {
            click: () => { this.addChild() },
          }
        })
    },
    //新增子件
    addChild(){
        let len = this.templateForm.productItems.length;
        let newNum = len?parseInt(this.templateForm.productItems[len-1].productItemNumber)+1:1;
        this.templateForm.productItems.push({'productItemNumber':newNum,'productItemName':'子件'+newNum,'productItemEnable':'1','productUuid':this.templateForm.productUuid})
    },
    delDisabled (index) {
      if(index!==0){
        return false
      }else{
        return true
      }
    },
    //删除子件
    delChild(index,item){
            this.templateForm.productItems.splice(index,1)

        // this.$confirm('确认删除'+item.name+'子件吗?', '警告', {
        //     type: 'warning'
        // }).then(() => {
        //     this.templateForm.child.splice(index,1)
        // }).catch(() => {})
    }
  }
}
</script>
<style lang="scss" scoped>
  .mar-l-80{
    margin-left: 80px;
  }
</style>



